<template>
  <div class="modify-password-container">
    <h3 class="header-title"></h3>
    <a-form>
      <a-form-item label="" required>
        <a-input type="password" v-model="modifyPasswordForm.oldPassword" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" required>
        <a-input type="password" v-model="modifyPasswordForm.newPassword" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" required>
        <a-input type="password" v-model="modifyPasswordForm.confirmPassword" placeholder=""></a-input>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" @click="handleModify"></a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
  export default {
    name: "ModifyPassword",
    data() {
      return {
        modifyPasswordForm: {}
      }
    },
    methods: {
      handleModify() {
        this.$api.auth.modifyPassword(this.modifyPasswordForm).then(() => {
          this.$message.success(this.$messages.successResult.operation);
          this.modifyPasswordForm = {};
        });
      }
    }
  }
</script>

<style scoped>
  .modify-password-container {
    width: 25%;
  }
</style>