<template>
  <div>
    <a-form hide-required-mark>
      <a-form-item label="ID" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-input v-model="userSearch.id" placeholder="ID"></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-input v-model="userSearch.keyword" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-select v-model="userSearch.gender" allowClear placeholder="">
          <a-select-option v-for="(label, code) in $messages.enums.gender" :key="code">{{label}}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-select v-model="userSearch.enabled" allowClear placeholder="">
          <a-select-option :value="1"></a-select-option>
          <a-select-option :value="0"></a-select-option>
        </a-select>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
  export default {
    name: "UserSearch",
    computed: {
      userSearch() {
        return this.$store.getters.search;
      }
    },
    watch: {
      userSearch(val) {
        this.$store.dispatch('setSearch', val);
      }
    },
  }
</script>

