<template>
  <div>
    <a-form>
      <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
        <a-input v-model="job.className" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="CRON" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
        <a-input v-model="job.cronExpression" placeholder="CRON"></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
        <a-input v-model="job.jobName" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
        <a-input v-model="job.jobGroup" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
        <a-input v-model="job.triggerName" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
        <a-input v-model="job.triggerGroup" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper">
        <a-input type="textarea" v-model="job.description" placeholder=""></a-input>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
  export default {
    name: "ScheduleForm",
    computed: {
      job() {
        return this.$store.getters.record;
      }
    },
    watch: {
      job(val) {
        this.$store.dispatch('setRecord', val);
      }
    }
  }
</script>

