<template>
  <div>
    <a-form hide-required-mark>
      <a-form-item label="ID" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-input v-model="scheduleSearch.id" placeholder="ID"></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-input v-model="scheduleSearch.keyword" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.searchForm.label" :wrapperCol="$style.searchForm.wrapper">
        <a-select v-model="scheduleSearch.paused" allowClear placeholder="">
          <a-select-option :value="1"></a-select-option>
          <a-select-option :value="0"></a-select-option>
        </a-select>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
  export default {
    name: "ScheduleSearch",
    computed: {
      scheduleSearch() {
        return this.$store.getters.search;
      },
      watch: {
        scheduleSearch(val) {
          this.$store.dispatch('setSearch', val);
        }
      }
    }
  }
</script>

