<template>
  <div>
    <a-form>
      <a-form-item label="" required>
        <a-radio-group v-model="message.msgType">
          <a-radio-button v-for="(label, msgType) in $messages.enums.wxMsgTypes" :key="msgType" :value="msgType">{{label}}</a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item label="" v-if="message.msgType === 'text'" required>
        <a-textarea v-model="message.content" :autosize="{minRows: 2, maxRows: 5}" placeholder=""></a-textarea>
      </a-form-item>
      <a-form-item label="MediaID" v-if="['image', 'voice', 'video'].indexOf(message.msgType) !== -1" required>
        <a-input v-model="message.mediaId" placeholder="mediaId"></a-input>
      </a-form-item>
      <div v-if="['video', 'music'].indexOf(message.msgType) !== -1">
        <a-form-item label="">
          <a-input v-model="message.title" placeholder=""></a-input>
        </a-form-item>
        <a-form-item label="">
          <a-textarea v-model="message.description" :autosize="{minRows: 2, maxRows: 5}" placeholder=""></a-textarea>
        </a-form-item>
      </div>
      <div v-if="message.msgType === 'music'">
        <a-form-item label="">
          <a-input v-model="message.misicUrl" placeholder=""></a-input>
        </a-form-item>
        <a-form-item label="">
          <a-input v-model="message.hqMusicUrl" placeholder=""></a-input>
        </a-form-item>
        <a-form-item label="ID" required>
          <a-input v-model="message.thumbMediaId" placeholder="ID"></a-input>
        </a-form-item>
      </div>
      <div v-if="message.msgType === 'news'">
        <a-form-item :label="'' + message.articles.length + ''">
          <div v-for="(article, index) in message.articles" :key="index" class="article-box">
            <div class="title-highlight-border">
               {{index + 1}}
              <a-popconfirm title="" trigger="click" placement="topRight" @confirm="removeArticle(index)"
                            :okText="$messages.operation.confirmText" :cancelText="$messages.operation.cancelText">
                <a-button type="danger" size="small" shape="circle" icon="delete" class="is-pulled-right"></a-button>
              </a-popconfirm>
            </div>
            <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
              <a-input v-model="article.title" placeholder=""></a-input>
            </a-form-item>
            <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
              <a-input v-model="article.title" placeholder=""></a-input>
            </a-form-item>
            <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
              <a-input v-model="article.title" placeholder=""></a-input>
            </a-form-item>
            <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
              <a-textarea v-model="article.title" :autosize="{minRows: 2, maxRows: 2}" placeholder=""></a-textarea>
            </a-form-item>
          </div>
          <a-button type="dashed" block icon="plus" @click="addArticle"></a-button>
        </a-form-item>
      </div>
    </a-form>
  </div>
</template>

<script>
  export default {
    name: "WxMessageForm",
    computed: {
      message() {
        return this.$store.getters.wxMessage;
      }
    },
    watch: {
      message(val) {
        this.$store.dispatch('setWxMessage', val);
      }
    },
    methods: {
      addArticle() {
        this.message.articles.push({});
      },
      removeArticle(index) {
        this.message.articles.splice(index, 1);
      }
    }
  }
</script>

<style scoped>
  .article-box {
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    padding: 8px;
    margin-bottom: 8px;
  }
</style>