<template>
  <div>
    <a-row>
      <a-col :span="18" :offset="3">
        <div class="page-content">
          <a-row class="toolbar">
            <a-button icon="plus" class="mr-1" @click="addFilterChain"></a-button>
            <a-button icon="reload" @click="handleRefresh"></a-button>
            <span class="is-pulled-right">
              <a-button icon="book" @click="infoVisible = true"></a-button>
            </span>
          </a-row>
          <a-spin :spinning="loading">
            <div class="filter-chain-container">
              <div v-for="(chain, index) in filterChains" :key="chain.id" class="filter-chain-box mb-1">
                <a-row>
                  <a-col :span="10">
                    <span><a-icon type="link"/> URL</span>
                    <a-input size="small" v-model="filterChain.url" v-if="showInput(chain.id)" class="edit-input"></a-input>
                    <b v-else>{{chain.url}}</b>
                  </a-col>
                  <a-col :span="10">
                    <span><a-icon type="filter"/> </span>
                    <a-input size="small" v-model="filterChain.filter" v-if="showInput(chain.id)" class="edit-input"></a-input>
                    <b v-else>{{chain.filter}}</b>
                  </a-col>
                  <a-col :span="4">
                    <template v-if="showInput(chain.id)">
                      <a-button size="small" icon="check" class="mr-1" @click="handleEdit"></a-button>
                      <a-button size="small" icon="close" @click="handleCancelEdit"></a-button>
                    </template>
                    <template v-else>
                      <a-row>
                        <a-col :span="12">
                          <a-button size="small" icon="arrow-up" class="mr-1" v-if="!isFirst(index)" @click="moveUp(index)"></a-button>
                          <a-button size="small" icon="arrow-down" v-if="!isLast(index)" @click="moveDown(index)"></a-button>
                        </a-col>
                        <a-col :span="12">
                          <div class="is-pulled-right">
                            <a-button size="small" icon="edit" class="mr-1" @click="editChain(chain)" ></a-button>
                            <a-popconfirm title="" trigger="click" placement="top" @confirm="handleDeleteFilterChain(chain.id)"
                                          :okText="$messages.operation.confirmText" :cancelText="$messages.operation.cancelText">
                              <a-button size="small" icon="delete"></a-button>
                            </a-popconfirm>
                          </div>
                        </a-col>
                      </a-row>
                    </template>
                  </a-col>
                </a-row>
              </div>
            </div>
          </a-spin>
        </div>
      </a-col>
    </a-row>

    <a-modal title=""
             :visible="infoVisible"
             :footer="null"
             @cancel="infoVisible = false">
      <div></div>
      <div><b>anon</b>,</div>
      <div><b>authc</b></div>
      <div><b>authcBasic</b>httpBasic</div>
      <div><b>logout</b></div>
      <div><b>perms</b> <code>/admins/user/**=perms["user:add:*,user:modify:*"]</code>
        isPermitedAll()</div>
      <div><b>port</b>URL8081 <code>schemal://serverName:8081?queryString</code>
        schmalhttphttpsserverNameHost8081PortqueryStringURL?</div>
      <div><b>rest</b> <code>/admins/user/**=perms[user:method]</code>methodpostgetdelete</div>
      <div><b>roles</b> <code>/admins/user/**=roles["admin,guest"]</code>
        hasAllRoles()</div>
      <div><b>ssl</b>,URLhttps</div>
      <div><b>user</b>,</div>
      <a-divider/>
      <div></div>
      <div><b>userauthc</b>userShirorememberMe
        rememberMeuserauthc
      </div>
    </a-modal>

    <a-modal title=""
             :maskClosable="false"
             :visible="addVisible"
             @ok="handleAddFilterChain"
             @cancel="addVisible = false">
      <a-form>
        <a-form-item label="URL" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
          <a-input v-model="filterChain.url" placeholder="URL"></a-input>
        </a-form-item>
        <a-form-item label="" :labelCol="$style.form.label" :wrapperCol="$style.form.wrapper" required>
          <a-input v-model="filterChain.filter" placeholder=""></a-input>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script>
  import Breadcrumb from "../../../../components/Breadcrumb";
  export default {
    name: "FilterChain",
    components: {Breadcrumb},
    data() {
      return {
        filterChains: [],
        loading: false,
        infoVisible: false,
        editMode: true,
        editId: '',
        editBtnLoading: false,
        addVisible: false,
        filterChain: {}
      }
    },
    created() {
      this.fetchData();
    },
    methods: {
      fetchData() {
        this.loading = true;
        this.$api.sys.getFilterChainList({orderByAsc: 'sort_code'}).then(res => {
          this.filterChains = res.data;
          this.loading = false;
        }).catch(() => {
          this.loading = false;
        });
      },
      addFilterChain() {
        this.filterChain = {};
        this.addVisible = true;
      },
      handleAddFilterChain() {
        this.$api.sys.addFilterChain(this.filterChain).then(() => {
          this.$message.success(this.$messages.successResult.create);
          this.addVisible = false;
          this.fetchData();
        });
      },
      handleDeleteFilterChain(chainId) {
        this.$api.sys.deleteFilterChain(chainId).then(() => {
          this.$message.success(this.$messages.successResult.delete);
          this.fetchData();
        });
      },
      handleRefresh() {
        this.handleCancelEdit();
        this.fetchData();
      },
      editChain(chain) {
        this.filterChain = JSON.parse(JSON.stringify(chain));
        this.editMode = true;
        this.editId = chain.id;
      },
      handleEdit() {
        this.editBtnLoading = true;
        this.$api.sys.updateFilterChain(this.filterChain.id, this.filterChain).then(() => {
          this.$message.success(this.$messages.successResult.edit);
          this.handleCancelEdit();
        }).catch(() => {
          this.handleCancelEdit();
        });
      },
      handleCancelEdit() {
        this.editMode = false;
        this.editId = '';
        this.editBtnLoading = false;
      },
      showInput(chainId) {
        return this.editMode && this.editId === chainId;
      },
      isFirst(index) {
        return index === 0;
      },
      isLast(index) {
        return index === this.filterChains.length - 1;
      },
      moveUp(index) {
        this.handleSwap(index, index - 1);
      },
      moveDown(index) {
        this.handleSwap(index, index + 1);
      },
      handleSwap(index1, index2) {
        let filterChains = this.filterChains;
        const tempSortCode = filterChains[index2].sortCode;
        filterChains[index2].sortCode = filterChains[index1].sortCode;
        filterChains[index1].sortCode = tempSortCode;
        this.$api.sys.batchSaveFilterChain([filterChains[index1], filterChains[index2]]).then(() => {
          this.$set(filterChains, index1, filterChains.splice(index2, 1, filterChains[index1])[0]);
          this.filterChains = filterChains;
        });
      }
    }
  }
</script>

<style scoped>
  .filter-chain-container {
    height: calc(100vh - 292px);
    overflow-y: auto;
  }
  .toolbar {
    line-height: 3rem;
  }
  .filter-chain-box {
    padding: 8px;
    border: 1px solid #e8e8e8;
    border-radius: 4px;
  }
  .edit-input {
    width: calc(100% - 90px);
  }
</style>